import './brandingPdf.css';
import PopUp from './popUp/popUp';
import { useState } from 'react';

export default function BrandingPdf({ imageSrc, title, description, link }) {
    let [ className, setClassName ] = useState('');
    let [ greg, setGreg ] = useState(false)
    function handlePopUp() {
        setClassName(greg ? '' : 'toggled');
        setGreg(!greg);
    }
  return (
    <div>
        <div className="card">
            <div className="imageContainer ">
              <img style={{width: "100%"}} className="img img-fluid" src={imageSrc} alt="" />{/*image size height x width 2112 x 1920*/}
            </div>
            <div className="card-body">
                <h5 className="card-title">{title}</h5>
                <p className="card-text">{description}</p>
                <button onClick={handlePopUp} className="btn btn-primary">Inpecter</button>
            </div>
        </div>
        <PopUp handlePopUp={handlePopUp} toggle={className} link={link} />
    </div>
  )
}
