import './popUp.css';

export default function PopUp({ toggle, link, handlePopUp }) {
    console.log(link)
  return (
    <div className={`${toggle} pdfFrame`}>
        <div className='closeBtn'>
            <i className='fa fa-close' onClick={handlePopUp}></i>
        </div>
        <iframe title='pdfReader' src={link} height="100%" width="100%"></iframe>
    </div>
  )
}
